// 
html {
  font-size: 14px;
}

// 
$font-size-xl: 1.5rem; // ~21px

// 
$spacer: 1rem;
// 
$spacer-x: $spacer;
// 
$spacer-y: $spacer;
// 
$spacer-2: $spacer * .5; // ~7px
// 
$spacer-1: $spacer * .25; // ~3.5px

// 
$white: #fff;
$black: #000;
$red: #d9534f;
$orange: #f0ad4e;
$yellow: #ffd500;
$green: #5cb85c;
$blue: #0275d8;
$teal: #5bc0de;
$pink: #ff5b77;
$purple: #613d7c;

// 
$gray-dark: #292b2c;
$gray: #464a4c;
$gray-light: #636c72;
$gray-lighter: #eceeef;
$gray-lightest: #f7f7f9;

// 
$brand-primary: $blue;
$brand-success: $green;
$brand-info: $teal;
$brand-warning: $orange;
$brand-danger: $red;
$brand-inverse: $gray-dark;

// 
$bg-opacity: .5;
// 
$bg-opacity-light: .6;
// 
$bg-opacity-lighter: .7;
// 
$bg-opacity-lightest: .95;

// 
$body-bg: #fcfcfc;

// 
$list-group-bg: rgba($white, $bg-opacity);
// 
$list-group-hover-bg: rgba(darken($gray-lightest, 5%), $bg-opacity);

// 
$breadcrumb-bg: rgba(darken($gray-lighter, 5%), $bg-opacity);

// 
$nav-tabs-active-link-hover-bg: rgba($body-bg, $bg-opacity-light);
// 
$nav-tabs-link-hover-border-color: darken($gray-lighter, 3%);

// 
$card-bg: rgba($white, $bg-opacity);
// 
$card-cap-bg: rgba(darken($gray-lightest, 2%), $bg-opacity);

// 
$pagination-bg: rgba($white, $bg-opacity-light);
// 
$pagination-disabled-bg: rgba($white, $bg-opacity-light);

// 
$input-bg: rgba($white, $bg-opacity-light);
// 
$custom-select-bg: rgba($white, $bg-opacity-light);

// 
$modal-header-border-color: rgba($black, .125);

// 
$blockquote-bg: rgba(darken($body-bg, 7%), $bg-opacity-lighter);
// 
$blockquote-border-width: .3rem;
// 
$blockquote-border-color: darken($body-bg, 11%);