// 
@include alert-bg-make(success, $alert-success-bg);
@include alert-bg-make(info, $alert-info-bg);
@include alert-bg-make(warning, $alert-warning-bg);
@include alert-bg-make(danger, $alert-danger-bg);

// 
.navbar {
  // 
  @include media-breakpoint-down(xs) {
    .container {
      width: 100%;
    }
  }

  // 
  .navbar-toggler {
    border-color: transparent;
    cursor: pointer;
  }

  // 
  .dropdown-toggle {
    color: $gray-dark;
    text-decoration: none;
  }
}

// 
.dropdown-menu {
  line-height: 2.2;
}

// 
.dropdown-item.active {
  color: #fff !important;
}

// 
.page-link {
  &:hover, &:focus {
    text-decoration: none;
  }
}

// 
.page-item.active {
  .page-link {
    min-width: 40px;
    text-align: center;

    &:hover {
      cursor: pointer;
    }
  }
}

// 
.blockquote {
  background-color: $blockquote-bg;
}

// 
.form-check.form-check-inline {
  margin-bottom: 0;
}

// 
.custom-select-sm {
  height: calc(#{$input-height * .95} + #{$border-width * 2});
  padding: 0 ($custom-select-padding-x * .9 + $custom-select-indicator-padding) 0 $custom-select-padding-x * .9;
  line-height: $custom-select-line-height * .95;
}